/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/packages              ->  index
 * POST    /api/packages              ->  create
 * GET     /api/packages/:id          ->  show
 * PUT     /api/packages/:id          ->  upsert
 * PATCH   /api/packages/:id          ->  patch
 * DELETE  /api/packages/:id          ->  destroy
 */

'use strict';

import jsonpatch from 'fast-json-patch';
import Package from './package.model';
import Q from 'q';
import _ from 'lodash';
import rp from 'request-promise';
import moment from 'moment';
var hotelCtrl = require('../hotel/hotel.controller.js');
var flightCtrl = require('../flight/flight.controller.js');
var eventCtrl = require('../event/event.controller.js');

var travelportHotelCtrl = require('../travelportHotel/travelportHotel.controller.js');


function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function (entity) {
    if (entity) {
      return res.status(statusCode).json(entity);
    }
    return null;
  };
}

function patchUpdates(patches) {
  return function (entity) {
    try {
      jsonpatch.apply(entity, patches, /*validate*/ true);
    } catch (err) {
      return Promise.reject(err);
    }

    return entity.save();
  };
}

function removeEntity(res) {
  return function (entity) {
    if (entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function (entity) {
    if (!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function (err) {
    res.status(statusCode).send(err);
  };
}


function formatDate(date) {
  return moment(date).format('YYYY-MM-DD');
}

function lookUpAirportByLatLong(lat, long) {
  var airportLookupUrl = 'http://terminal2.expedia.com/x/geo/features?within=30km&lat=' + lat + '&lng=' + long + '&type=airport&verbose=3&apikey=BAGEROtURYYysKTHQIE7HK5m0tOFIjSH';
  return rp(airportLookupUrl);
}

function getAllData(id, lat, long, departureAirport, departureDate, eventId, arrivalDate, adults, childrens) {

  return lookUpAirportByLatLong(lat, long).then(dat => {
    let arrivalAirport = JSON.parse(dat)[0].tags.iata.airportCode.value;
    return Q.all([
        travelportHotelCtrl.getAllHotels(arrivalAirport,
          formatDate(departureDate),
          formatDate(arrivalDate),
          lat, long, adults, childrens),
        eventCtrl.getEventOffer(eventId)
      ]).then(function (hotels) {
        const hotelArr = hotels[0][0];
        return travelportHotelCtrl.getHotelRates(hotelArr, arrivalAirport,
            formatDate(departureDate),
            formatDate(arrivalDate),
            lat, long, adults, childrens)
          .then(hot => {
            let mergedHotels = _.map(hot, function (item, i) {
              return _.merge(item, hotelArr[i]);
            });
            hotels[0][0] = _.filter(mergedHotels, function (o) {
              return o.state === 'fulfilled';
            });
            return hotels;
          });
      })
      .then(function (data) {
        return Q.allSettled([
            _package(3, data, arrivalAirport,
              formatDate(departureDate),
              formatDate(arrivalDate),
              lat, long, adults, childrens),
            _package(2, data, arrivalAirport,
              formatDate(departureDate),
              formatDate(arrivalDate),
              lat, long, adults, childrens),
            _package(1, data, arrivalAirport,
              formatDate(departureDate),
              formatDate(arrivalDate),
              lat, long, adults, childrens),
            _package(0, data, arrivalAirport,
              formatDate(departureDate),
              formatDate(arrivalDate),
              lat, long, adults, childrens)
          ])
          .then(function (results) {
            return {
              'Basic Package': results[0],
              'Middle Package': results[1],
              'Premium Package': results[2],
              'VIP Package': results[3]
            };
          });
      });
  });
}



